*{
  border-radius: 25px;
}
div#chat-display {
  margin: 15px;
  border: 1px solid lightgray;
}
div#buttons {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  background: lightgray;
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
  padding: 10px;
}
.disabled{
  opacity: 0.2;
}
h1 {
  margin: 0;
}
div#contact-icon {
  color:white;
  background: gray;
  padding-left: 15px;
  padding-right: 15px;
}
div#messages {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  border-top-left-radius: 0px;
  border-top-right-radius: 0px;
  padding: 10px;
}
div#bubble {
    padding: 5px;
    margin: 5px;
}
div#from-container > div#bubble{
  background: rgb(51, 204, 255);
}
div#to-container > div#bubble{
  background: rgb(228, 228, 228);
}
div#to-container{
  width: 100%;
  display: inherit;
  -ms-flex-pack: end;
      justify-content: flex-end;
}
div#from-container{
  width: 100%;
  display: inherit;
  -ms-flex-pack: start;
      justify-content: flex-start;
}
button#load-more{
  opacity: 0;
}
button#load-more:focus{
  outline:0;
}



